'use strict';

const registerButton = document.querySelector('[data-but="register"');
const registerBlock = document.querySelector('.register-block');
const mainInfo = document.querySelector('.main-info');
const header = document.querySelector('.header');

registerButton.addEventListener('click', () => {
  registerBlock.classList.remove('register-block--is-hidden');
  mainInfo.classList.add('main-info--is-hidden');
  header.classList.add('header--is-hidden');
});

const closeFormIcon = document.querySelector('.selector-form__close-form');
const closeFormButton = document.querySelector('[data-but="close-button"]');

closeFormIcon.addEventListener('click', () => {
  registerBlock.classList.add('register-block--is-hidden');
  mainInfo.classList.remove('main-info--is-hidden');
  header.classList.remove('header--is-hidden');
});

closeFormButton.addEventListener('click', () => {
  registerBlock.classList.add('register-block--is-hidden');
  mainInfo.classList.remove('main-info--is-hidden');
  header.classList.remove('header--is-hidden');
});

const nextButton = document.querySelector('.button__next-button');
const selectorFormFirst = document.querySelector('.selector-form--first');
const selectorFormSecond = document.querySelector('.selector-form--second');

const backButton = document.querySelector('[data-but="back-button"]');

nextButton.addEventListener('click', () => {
  selectorFormFirst.classList.add('selector-form--is-hidden');
  selectorFormSecond.classList.remove('selector-form--is-hidden');
});

backButton.addEventListener('click', () => {
  selectorFormFirst.classList.remove('selector-form--is-hidden');
  selectorFormSecond.classList.add('selector-form--is-hidden');
});

const inputName = document.querySelector('[data-input="name"]');

inputName.addEventListener('blur', (event) => {
  if (event.target.value === '') {
    document.querySelector('[data-placeholder="fieldName"]')
      .classList.add('register__input--placeholder--is-error');
    inputName.classList.add('register__input--is-error');
  }
});

inputName.addEventListener('focus', () => {
  inputName.classList.remove('register__input--is-error');
});

const inputSurname = document.querySelector('[data-input="surname"]');

inputSurname.addEventListener('blur', (event) => {
  if (event.target.value === '') {
    document.querySelector('[data-placeholder="fieldSurname"]')
      .classList.add('register__input--placeholder--is-error');
    inputSurname.classList.add('register__input--is-error');
  }
});

inputSurname.addEventListener('focus', () => {
  inputSurname.classList.remove('register__input--is-error');
});

const inputEmail = document.querySelector('[data-input="email"]');

inputEmail.addEventListener('blur', (event) => {
  if (event.target.value === '') {
    document.querySelector('[data-placeholder="fieldEmail"]')
      .classList.add('register__input--placeholder--is-error');
    inputEmail.classList.add('register__input--is-error');
  }
});

inputEmail.addEventListener('focus', () => {
  inputEmail.classList.remove('register__input--is-error');
});

const inputTel = document.querySelector('[data-input="tel"]');

inputTel.addEventListener('blur', (event) => {
  if (event.target.value === '') {
    document.querySelector('[data-placeholder="fieldTel1"]')
      .classList.add('register__input--placeholder--is-error');

    document.querySelector('[data-placeholder="fieldTel2"]')
      .classList.add('register__input--placeholder--is-error');
    inputTel.classList.add('register__input--is-error');
  }
});

inputTel.addEventListener('focus', () => {
  inputTel.classList.remove('register__input--is-error');
});

const registerMain = document.querySelector('[data-but="register-main"]');

registerMain.addEventListener('click', (event) => {
  event.preventDefault();
  document.querySelector('form').reset();
});
